<template>
  <v-container fluid grid-list-sm>
    <!-- snackbar -->
    <v-snackbar v-model="snackbar" color="failure" :timeout="5000" top>
      <span>{{error}}</span>
      <v-btn flat color="white" @click="snackbar=false">
        <v-icon>close</v-icon>
      </v-btn>
    </v-snackbar>
    <!-- feedback -->

    <span>{{feedback}}</span>

    <v-container fluid grid-list-sm>
      <v-layout row wrap>
        <v-flex grow v-for="photo in photos" :key="photo.id" xs6 md4>
          <img
            v-if="photo.img_src"
            :src="getImgUrl(photo.img_src)"
            class="image"
            :alt="photo.camera.full_name + photo.earth_date"
            width="100%"
            height="100%"
          >
        </v-flex>
      </v-layout>
    </v-container>
  </v-container>
</template>

<script>
import Photo from "@/components/PhotoComponent";

export default {
  data() {
    return {
      snackbar: false,
      timeout: 10000
    };
  },
  methods: {
    //v-img wont show if the url is directly bound in the template
    getImgUrl: function(img) {
      return img;
    }
  },
  computed: {
    photos() {
      if (this.$store.getters.getPhotos) {
        return this.$store.getters.getPhotos;
      }
    },
    feedback() {
      if (this.$store.getters.getFeedback) {
        return this.$store.getters.getFeedback;
      }
    },
    error() {
      if (this.$store.getters.getError) {
        this.snackbar = true;
        return this.$store.getters.getError;
      }
    }
  }
};
</script>

<style>
.gallery-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
</style>
